<template>
  <div class="payTransfer">
    <el-form ref="form" :model="addForm" label-width="200px" v-if="stepNextBack === 1">
      <div class="pay-transfer-header mb-20 mt-15">请设置查询条件</div>

      <el-form-item label="开户行别：">
        <el-select v-model="addForm.accountType" style="width:260px;">
          <el-option label="全部" value></el-option>
          <el-option label="建行" value="jh"></el-option>
          <el-option label="他行" value="others"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="账户类别名称：">
        <el-select v-model="addForm.accountType" style="width:260px;">
          <el-option label="全部" value></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="优先级：">
        <el-input type="text" style="width:260px;" v-model="addForm.levalSet"></el-input>
      </el-form-item>

      <el-form-item label="户名：" prop="accountName">
        <el-input type="text" style="width:260px;" v-model="addForm.accountName"></el-input>
      </el-form-item>

      <el-form-item label="开户行：" prop="opneBankName">
        <el-input type="text" style="width:260px;" v-model="addForm.opneBankName"></el-input>
      </el-form-item>

      <el-form-item label="账户别名：" prop="ownSet">
        <el-input type="text" style="width:260px;" v-model="addForm.ownSet"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button
          size="mini"
          type="primary"
          style="background-color: #1f93d5;border-color: #1f93d5;"
          @click="searchInfo"
        >查询</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="detailData" border highlight-current-row fit size="mini" @selection-change="chosePaper" v-if="stepNextBack === 2">
      <el-table-column type="selection" align="center" />
      <el-table-column label="账号" prop="account" align="center"></el-table-column>
      <el-table-column label="户名" prop="accountName" align="center"></el-table-column>
      <el-table-column label="行别" prop="payName" align="center"></el-table-column>
      <el-table-column label="开户行" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.payName === '建行'">{{ scope.row.opneBankName }}</span>
          <span v-if="scope.row.payName === '他行'">{{ scope.row.websitepoint }}</span>
        </template>
      </el-table-column>
      <el-table-column label="账户别名" prop="ownSet" align="center"></el-table-column>
      <el-table-column label="优先级" prop="levalSet" align="center"></el-table-column>
      <el-table-column label="账户类别" prop="accountType" align="center" :formatter="idToName"></el-table-column>
    </el-table>

    <el-table
      key="modTable"
      :data="modData"
      border
      highlight-current-row
      fit
      size="mini"
      v-if="stepNextBack === 3"
    >
      <el-table-column label="账号" prop="account" align="center"></el-table-column>
      <el-table-column label="户名" prop="accountName" align="center"></el-table-column>
      <el-table-column label="行别" prop="payName" align="center"></el-table-column>
      <el-table-column label="开户行" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.payName === '建行'">{{ scope.row.opneBankName }}</span>
          <span v-if="scope.row.payName === '他行'">{{ scope.row.websitepoint }}</span>
        </template>
      </el-table-column>
      <el-table-column label="账户别名" align="center">
        <template slot-scope="scope">
          <el-input v-model="scope.row.ownSet" placeholder="账户别名"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="优先级" align="center">
        <template slot-scope="scope">
          <el-input v-model="scope.row.levalSet" placeholder="优先级"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="账户类别" align="center">
        <template slot-scope="scope">
          <el-select v-model="scope.row.accountType">
            <el-option v-for="(item,index) of typeList" :key="index" :label="item.accountType" :value="item.levalSet"></el-option>
          </el-select>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top: 20px;" v-if="stepNextBack === 2">
      <el-button
        size="mini"
        type="primary"
        style="background-color: #1f93d5;border-color: #1f93d5;"
        @click="modInfo"
      >修改</el-button>
    </div>

    <el-form ref="pwdForm" :model="pwdForm" :rules="rule" label-width="200px" v-if="stepNextBack === 3">
      <div class="pay-transfer-header mb-20 mt-15">请输入认证信息</div>
      <el-form-item label="交易密码：" prop="pwd">
        <el-input v-model="pwdForm.pwd" style="width:260px;" type="password" auto-complete="new-password"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="modSave">确定</el-button>
        <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="modBack">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import elDragDialog from '@/directive/el-drag-dialog' // base on element-ui
export default {
  name: 'add',
  directives: { elDragDialog },
  props: {
    stepNextBack: {
      type: Number,
      required: true,
      defaults: 1
    }
  },
  data () {
    return {
      typeList: [],
      modData: [],
      accountType: '',
      levalSet: '',
      selectdeVal: '',
      addForm: {
        account: '',
        accountName: '',
        opneBankName: '',
        ownSet: '',
        accountType: '',
        levalSet: ''
      },
      pwdForm: {
        pwd: ''
      },
      rule: {
        pwd: [{ required: true, message: '请填交易密码', trigger: 'blur' }]
      }
    }
  },
  computed: {
    detailData () {
      return this.$store.state.payeeInfo
    },
    typeInfo () {
      return this.$store.state.typeInfo
    }
  },
  watch: {
    typeInfo (value) {
      this.typeList = value
    }
  },
  created () {

  },
  methods: {
    idToName (val) {
      for (const one of this.typeList) {
        if (one.levalSet.toString() === val.accountType.toString()) {
          return one.accountType
        }
      }
    },
    chosePaper (val) {
      this.selectdeVal = val
    },
    searchInfo () {
      this.$emit('changeStepNextBack', 2)
    },
    modInfo () {
      if (this.selectdeVal.length === 0) {
        this.$message.error('请选择修改数据')
      } else if (this.selectdeVal.length > 1) {
        this.$message.error('一次只能修改一条数据')
      } else {
        this.modData = this.selectdeVal
        this.$emit('changeStepNextBack', 3)
        this.selectdeVal = []
      }
    },
    modSave () {
      this.$refs.pwdForm.validate((valid) => {
        if (valid) {
          if (this.pwdForm.pwd === this.$store.state.userInfo.password) {
            // delete this.pwdForm.pwd

            const data = { ...this.modData[0] }
            // 检测是否重复
            // let bool = true
            // if (this.payeeList.length > 0) {
            //   this.payeeList.forEach(v => {
            //     if (v.account === data.account) {
            //       bool = false
            //     }
            //   })
            // }
            // if (!bool) {
            //   this.$message.warning('收款方账号不允许重复')
            //   return false
            // }
            // if (data.id >= 0) {
            // 编辑
            this.$store.commit('editPayeeInfo', data)
            this.$message.success('编辑成功')
            // this.resetPayee()
            // } else {
            //   // 新增
            //   this.$store.commit('savePayeeInfo', data)
            //   this.$message.success('新增成功')
            // }
            // this.dialogConfirmVisible = false
            // this.dialogUKVisible = false
            // delete this.addForm.id
            // this.resetAddForm()
            this.$refs.pwdForm.resetFields()
          } else {
            this.$message.error('密码错误')
          }
        }
      })
    },
    modBack () {
      this.$emit('changeStepNextBack', 2)
    }
  }
}
</script>

<style lang="scss" scoped>
.payTransfer {
  /deep/ .el-input__inner {
    padding: 0 5px;
  }
  .w-400 {
    width: 400px;
  }
  .header {
    padding: 20px 10px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
  }
  .pay-transfer-header {
    background-color: #f8f8f8;
    padding: 5px 10px;
  }

  .password-input {
    position: absolute;
    left: 119px;
    width: 80px;
    top: 5px;
  }
  .password-button {
    position: absolute;
    left: 260px;
    width: 75px;
    height: 25px;
    top: 155px;
    cursor: pointer;
  }
  .content .makingContent .rightBottomLine {
    border-bottom: 1px #c5d7e8 solid;
    border-right: 1px #c5d7e8 solid;
  }
  .content .makingContent td {
    height: 40px;
    font-size: 15px;
    text-align: center;
  }
  .content .makingContent .BottomLine {
    border-bottom: 1px #c5d7e8 solid;
  }
}
</style>
