<template>
  <div class="payTransfer">
    <el-form ref="addForm" :model="addForm" :rules="rule" label-width="200px" v-if="stepNextBack === 1">
      <el-form-item label=" 请选择收款人：" prop="payName">
        <el-radio-group v-model="addForm.payName">
          <el-radio :label="'建行'">收款人为建行（单笔新增）</el-radio>
          <el-radio :label="'他行'">收款人为他行（单笔新增）</el-radio>
          <el-radio :label="'others'" disabled>收款人为他行（批量新增）</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="账号：" prop="account">
        <el-input type="text" style="width:260px;" v-model="addForm.account"></el-input>
      </el-form-item>

      <el-form-item label="户名：" prop="accountName">
        <el-input type="text" style="width:260px;" v-model="addForm.accountName"></el-input>
      </el-form-item>

      <el-form-item label="开户行省/市：" prop="openLocation" v-if="addForm.payName === '建行'">
        <el-input type="text" style="width:260px;" v-model="addForm.openLocation"></el-input>
      </el-form-item>

      <el-form-item label="开户行名称：" prop="opneBankName" v-if="addForm.payName === '建行'">
        <el-input type="text" style="width:260px;" v-model="addForm.opneBankName"></el-input>
      </el-form-item>

      <el-form-item v-if="addForm.payName === '他行' || addForm.payName === 'others'">
        <el-radio-group v-model="addForm.inputType">
          <el-radio :label="'choose'">快速选择</el-radio>
          <el-radio :label="'inHand'" disabled>手工输入</el-radio>
        </el-radio-group>
      </el-form-item>

      <!-- <el-form-item label="开户行：" :required="true" v-if="addForm.payName === '他行' || addForm.payName === 'others'">
        <div>
          <el-select v-model="addForm.bank" placeholder="请选择银行" style="width: 250px" @change="choseBank">
            <el-option v-for="item in bankList" :key="item.key" :label="item.value" :value="item.key"></el-option>
          </el-select>
          <el-input type="text" style="width:240px;margin-left: 5px;" v-model="addForm.opneBankName"></el-input>
          <span style="margin-left: 10px;"> 模糊查询 在选择开户行之后，您可输入关键字或联行号查找具体网点</span>
        </div>

        <div style="margin-top: 10px;">
          <el-select v-model="addForm.province" placeholder="请选择省" @change="choseCity" style="width: 250px">
            <el-option v-for="item in provinceList" :key="item" :label="item" :value="item"></el-option>
          </el-select>
          <el-select v-model="addForm.city" placeholder="请选择市" style="width: 250px;margin-left: 10px;" @change="getSearBank">
            <el-option v-for="item in cityList" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </div>
        <div style="margin-top: 10px;">
          <el-select v-model="addForm.websitepoint" placeholder="请选择网点" style="width: 350px" @change="getSearBank">
            <el-option v-for="item in bankNames" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </div>
      </el-form-item> -->
      <el-form-item prop="bank" v-if="addForm.payName === '他行' || addForm.payName === 'others'">
        <el-select v-model="addForm.bank" placeholder="请选择银行" style="width: 250px" @change="choseBank">
          <el-option v-for="item in bankList" :key="item.key" :label="item.value" :value="item.key"></el-option>
        </el-select>
        <el-input type="text" style="width:240px;margin-left: 5px;" v-model="addForm.opneBankName"></el-input>
        <span style="margin-left: 10px;"> 模糊查询 在选择开户行之后，您可输入关键字或联行号查找具体网点</span>
      </el-form-item>
      <span style="position: relative;top: 28px;left: 120px;height: 15px;color: #F56C6C;" v-if="addForm.payName === '他行' || addForm.payName === 'others'">*</span>
      <el-form-item label="开户行：" v-if="addForm.payName === '他行' || addForm.payName === 'others'">
          <el-form-item prop="province" style="width:250px;float:left;">
            <el-select v-model="addForm.province" placeholder="请选择省" @change="choseCity" style="width: 250px">
              <el-option v-for="item in provinceList" :key="item" :label="item" :value="item"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item prop="city" style="width:250px;float:left;margin-left: -5px;">
            <el-select v-model="addForm.city" placeholder="请选择市" style="width: 250px;margin-left: 10px;" @change="getSearBank">
              <el-option v-for="item in cityList" :key="item" :label="item" :value="item"></el-option>
            </el-select>
          </el-form-item>

      </el-form-item>

      <el-form-item prop="websitepoint" v-if="addForm.payName === '他行' || addForm.payName === 'others'">
        <el-select v-model="addForm.websitepoint" placeholder="请选择网点" style="width: 350px" @change="getSearBank">
          <el-option v-for="item in bankNames" :key="item" :label="item" :value="item"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="自定义别名：">
        <el-input type="text" style="width:260px;" v-model="addForm.ownSet"></el-input>
        <span style="margin-left: 10px;">自定义别名可根据管理需要自行设置账户别名</span>
      </el-form-item>

      <el-form-item label="账户类别名称：">
        <el-select v-model="addForm.accountType" style="width:260px;">
          <el-option v-for="(item,index) of typeList" :key="index" :label="item.accountType" :value="item.accountType"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="优先级：">
        <el-input type="text" style="width:260px;" v-model="addForm.levalSet"></el-input>
        <span style="margin-left: 10px;">优先级可以是0-9999的整数，优先级由0至9999依次降低，优先级高的将在常用收款账户列表中靠前显示</span>
      </el-form-item>

      <div class="pay-transfer-header mb-20 mt-15">请输入认证信息</div>
      <el-form-item label="交易密码：" prop="pwd">
        <el-input v-model="addForm.pwd" style="width:260px;" type="password" auto-complete="new-password"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="addInfo">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import elDragDialog from '@/directive/el-drag-dialog' // base on element-ui
import { getProvince, getCity, getBankName } from '@/utils'
// , getBank, resetOneArray, treeToOneArray
export default {
  name: 'add',
  directives: { elDragDialog },
  computed: {
    bankList () {
      return this.$store.state.bank
    },
    payeeGroupList () {
      return this.$store.state.payeeGroupList
    },
    userInfo () {
      return this.$store.state.userInfo
    },
    payeeList () {
      return this.$store.state.payeeInfo
    },
    typeInfo () {
      return this.$store.state.typeInfo
    }
  },
  watch: {
    payeeList (value) {
      this.searchResultData = value
    },
    typeInfo (value) {
      this.typeList = value
    }
  },
  data () {
    const isNumber = (rule, value, callback) => { // 验证数字
      var numReg = /^[0-9]*$/
      var numRe = new RegExp(numReg)
      if (!numRe.test(value)) {
        callback(new Error('账号只能由纯数字组成'))
      } else {
        callback()
      }
    }
    return {
      bankNames: [],
      provinceList: [],
      city: null,
      cityList: [],
      typeList: [],
      stepNextBack: 1,
      checked: false,
      addForm: {
        city: '',
        websitepoint: '',
        bank: '',
        province: null,
        account: '',
        accountName: '',
        openLocation: '',
        opneBankName: '',
        pwd: '',
        ownSet: '',
        accountType: '',
        levalSet: '',
        payName: '建行',
        inputType: 'choose'
      },
      rule: {
        bank: [{ required: true, message: '请选择银行', trigger: 'change' }],
        account: [{ required: true, message: '请填账号', trigger: 'blur' }, { validator: isNumber, trigger: 'blur' }],
        accountName: [{ required: true, message: '请填户名', trigger: 'blur' }],
        province: [{ required: true, message: '请填开户行省/市', trigger: 'blur' }],
        provinceOther: [{ required: true, message: '请选择开户行省', trigger: 'change' }],
        city: [{ required: true, message: '请选择开户行市', trigger: 'change' }],
        websitepoint: [{ required: true, message: '请选择开户行网点', trigger: 'change' }],
        opneBankName: [{ required: true, message: '请填开户行名称', trigger: 'blur' }],
        pwd: [{ required: true, message: '请填交易密码', trigger: 'blur' }],
        payName: [{ required: true, message: '请选择付款人', trigger: 'change' }]
      }
    }
  },
  created () {
    this.typeList = this.typeInfo
  },
  methods: {
    // 选择银行
    choseBank () {
      this.choseBankVisible = true
      this.$nextTick(() => {
        this.addForm.province = null
        this.addForm.city = null
        this.searchList = []
        this.total = 0
        this.provinceList = getProvince(this.addForm.bank)
      })
    },
    // 根据省获取市
    choseCity () {
      this.cityList = getCity(this.addForm.bank, this.addForm.province)
    },
    getSearBank () {
      this.bankNames = getBankName(this.addForm.bank, this.addForm.province, this.addForm.city)

      if (this.bankNames.length > 0) {
        this.addForm.websitepoint = this.bankNames[0]
      }
    },
    addInfo () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          if (this.addForm.pwd === this.$store.state.userInfo.password) {
            delete this.addForm.pwd

            const data = { ...this.addForm }
            // 检测是否重复
            let bool = true
            if (this.payeeList.length > 0) {
              this.payeeList.forEach(v => {
                if (v.account === data.account) {
                  bool = false
                }
              })
            }
            if (!bool) {
              this.$message.warning('收款方账号不允许重复')
              return false
            }
            if (data.id >= 0) {
              // 编辑
              this.$store.commit('editPayeeInfo', data)
              this.$message.success('编辑成功')
              this.resetPayee()
            } else {
              // 新增
              this.$store.commit('savePayeeInfo', data)
              this.$message.success('新增成功')
            }
            this.dialogConfirmVisible = false
            this.dialogUKVisible = false
            delete this.addForm.id
            this.resetAddForm()
            // this.$refs.addForm.resetFields()
          } else {
            this.$message.error('密码错误')
          }
        }
      })
    },
    resetAddForm () {
      this.addForm = {
        city: '',
        websitepoint: '',
        bank: '',
        province: null,
        account: '',
        accountName: '',
        openLocation: '',
        opneBankName: '',
        pwd: '',
        ownSet: '',
        accountType: '',
        levalSet: '',
        payName: '建行',
        inputType: 'choose'
      }
    }
  }
}
</script>

