<template>
  <div class="payTransfer">
    <el-form ref="form" :model="searchForm" label-width="200px" v-if="stepNextBack === 1">
      <div class="pay-transfer-header mb-20">请设置查询条件</div>

      <el-form-item label="开户行别：" prop="bankType">
        <div>
          <el-select v-model="searchForm.bankType" placeholder="全部" style="width:260px;">
            <el-option label="全部" value="all"></el-option>
            <el-option label="建行" value="jh"></el-option>
            <el-option label="他行" value="other"></el-option>
          </el-select>
        </div>
      </el-form-item>

      <el-form-item label="账户类别：" prop="accountType">
        <div style="margin-top: 10px;">
          <el-select v-model="searchForm.accountType" style="width:260px;">
            <el-option label="全部" value=""></el-option>
          </el-select>
        </div>
      </el-form-item>

      <el-form-item label="优先级：">
        <el-input type="text" readonly style="width:260px;" v-model="searchForm.account_leval"></el-input>
      </el-form-item>

      <el-form-item label="户名：">
        <el-input type="text" readonly style="width:260px;" v-model="searchForm.accountName"></el-input>
      </el-form-item>

      <el-form-item label="账号：">
        <el-input type="text" readonly style="width:260px;" v-model="searchForm.account"></el-input>
      </el-form-item>

      <el-form-item label="开户行：">
        <el-input type="text" readonly style="width:260px;" v-model="searchForm.openBank"></el-input>
      </el-form-item>

      <el-form-item label="账户别名：">
        <el-input type="text" readonly style="width:260px;" v-model="searchForm.acBankName"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="search">查询</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="listData" border highlight-current-row fit size="mini" v-if="stepNextBack === 2">
      <el-table-column label="账号" prop="account" align="center"></el-table-column>
      <el-table-column label="户名" prop="accountName" align="center"></el-table-column>
      <el-table-column label="行别" prop="payName" align="center"></el-table-column>
      <el-table-column label="开户行" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.payName === '建行'">{{ scope.row.opneBankName }}</span>
          <span v-if="scope.row.payName === '他行'">{{ scope.row.websitepoint }}</span>
        </template>
      </el-table-column>
      <el-table-column label="账户别名" prop="ownSet" align="center"></el-table-column>
      <el-table-column label="优先级" prop="levalSet" align="center"></el-table-column>
      <el-table-column label="账户类别" prop="accountType" align="center"></el-table-column>
    </el-table>
    <div style="text-align: center;margin-top: 20px">
      <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="goBack" v-if="stepNextBack === 2">返回</el-button>
    </div>
  </div>
</template>

<script>
import elDragDialog from '@/directive/el-drag-dialog' // base on element-ui
export default {
  name: 'search',
  directives: { elDragDialog },
  computed: {
    detailData () {
      return this.$store.state.payeeInfo
    }
  },
  props: {
    stepNextBack: {
      type: Number,
      required: true,
      defaults: 1
    }
  },
  watch: {

  },
  data () {
    return {
      listData: [],
      searchForm: {
        bankType: '',
        accountType: '',
        account_leval: '',
        accountName: '',
        account: '',
        openBank: '',
        acBankName: ''
      }
    }
  },
  created () {
    this.initData()
  },
  methods: {
    initData () {
      // 进行行别的筛选
      let _v = []
      if (this.searchForm.bankType === 'jh') {
        this.detailData.forEach(item => {
          if (item.payName === '建行') {
            _v.push(item)
          }
        })
      } else if (this.searchForm.bankType === 'other') {
        this.detailData.forEach(item => {
          if (item.payName === '他行') {
            _v.push(item)
          }
        })
      } else {
        _v = this.detailData
      }
      this.listData = _v
      this.listData.sort(this.compare('levalSet'))
    },
    search () {
      this.initData()
      this.$emit('changeStepNextBack', 2)
    },
    goBack () {
      this.$emit('changeStepNextBack', 1)
    },
    compare (attr) {
      return function (a, b) {
        var val1 = a[attr]
        var val2 = b[attr]
        return val1 - val2
        // return val2 - val1
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.payTransfer {
  /deep/ .el-input__inner {
    padding: 0 5px;
  }
  .w-400 {
    width: 400px;
  }
  .header {
    padding: 20px 10px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
  }
  .pay-transfer-header {
    background-color: #f8f8f8;
    padding: 5px 10px;
  }
  .password-input {
    position: absolute;
    left: 119px;
    width: 80px;
    top: 5px;
  }
  .password-button {
    position: absolute;
    left: 260px;
    width: 75px;
    height: 25px;
    top: 155px;
    cursor: pointer;
  }
  .content .makingContent .rightBottomLine{
    border-bottom: 1px #c5d7e8 solid;
    border-right: 1px #c5d7e8 solid;
  }
  .content .makingContent td {
    height: 40px;
    font-size: 15px;
    text-align: center;
  }
  .content .makingContent .BottomLine {
    border-bottom: 1px #c5d7e8 solid;
  }
}
</style>
