<template>
  <div class="payTransfer">
    <el-form
      ref="form"
      :model="addForm"
      label-width="200px"
      v-if="stepNextBack === 1"
    >
      <div class="pay-transfer-header mb-20 mt-15">请设置查询条件</div>

      <el-form-item label="开户行别：">
        <el-select v-model="addForm.accountType" style="width:260px;">
          <el-option label="全部" value></el-option>
          <el-option label="建行" value="jh"></el-option>
          <el-option label="他行" value="others"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="账户类别名称：">
        <el-select v-model="addForm.accountType" style="width:260px;">
          <el-option label="全部" value=""></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="优先级：">
        <el-input type="text" style="width:260px;" v-model="addForm.levalSet"></el-input>
      </el-form-item>

      <el-form-item label="户名：" prop="accountName">
        <el-input type="text" style="width:260px;" v-model="addForm.accountName"></el-input>
      </el-form-item>

      <el-form-item label="开户行：" prop="opneBankName">
        <el-input type="text" style="width:260px;" v-model="addForm.opneBankName"></el-input>
      </el-form-item>

      <el-form-item label="账户别名：" prop="ownSet">
        <el-input type="text" style="width:260px;" v-model="addForm.ownSet"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button
          size="mini"
          type="primary"
          style="background-color: #1f93d5;border-color: #1f93d5;"
          @click="searchInfo"
        >查询</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="detailData" border highlight-current-row fit size="mini" @selection-change="chosePaper" v-if="stepNextBack === 2">
      <el-table-column type="selection" align="center" />
      <el-table-column label="账号" prop="account" align="center"></el-table-column>
      <el-table-column label="户名" prop="accountName" align="center"></el-table-column>
      <el-table-column label="行别" prop="payName" align="center"></el-table-column>
      <el-table-column label="开户行" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.payName === '建行'">{{ scope.row.opneBankName }}</span>
          <span v-if="scope.row.payName === '他行'">{{ scope.row.websitepoint }}</span>
        </template>
      </el-table-column>
      <el-table-column label="账户别名" prop="ownSet" align="center"></el-table-column>
      <el-table-column label="优先级" prop="levalSet" align="center"></el-table-column>
      <el-table-column label="账户类别" prop="accountType" align="center"></el-table-column>
    </el-table>

    <el-form ref="pwdForm" :model="pwdForm" :rules="rule" label-width="200px" v-if="stepNextBack === 2" style="margin-top: 15px;" key="pwdForm">
      <div class="pay-transfer-header mb-20">请输入认证信息</div>

      <el-form-item label="交易密码：" prop="pwd">
        <el-input type="password" size="mini" style="width:260px;" v-model="pwdForm.pwd"></el-input>
      </el-form-item>
      <el-form-item style="margin-top: 20px;">
        <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="delInfo">删除</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import elDragDialog from '@/directive/el-drag-dialog' // base on element-ui
export default {
  name: 'add',
  directives: { elDragDialog },
  props: {
    stepNextBack: {
      type: Number,
      required: true,
      defaults: 1
    }
  },
  computed: {
    payeeInfo () {
      return this.$store.state.payeeInfo
    }
  },
  watch: {
    payeeInfo (value) {
      this.detailData = value
    }
  },
  data () {
    return {
      detailData: [],
      multiSelection: [],
      pwdForm: {
        pwd: ''
      },
      addForm: {
        account: '',
        accountName: '',
        opneBankName: '',
        ownSet: '',
        accountType: '',
        levalSet: ''
      },
      rule: {
        pwd: [{ required: true, message: '请填交易密码', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.getList()
  },
  methods: {
    getList () {
      this.detailData = this.payeeInfo
    },
    delInfo () {
      if (this.multiSelection.length === 0) {
        this.$message.error('请选择需要删除数据')
      } else {
        if (this.pwdForm.pwd === this.$store.state.userInfo.password) {
          this.$confirm('此操作将删除类别数据, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            const data = []
            this.multiSelection.forEach(v => {
              data.push(v.account)
            })
            this.$store.commit('delPayee', data)
            this.$message.success('删除成功')
            this.pwdForm.pwd = ''
          }).catch(() => {
            console.log('cancel')
          })
        } else {
          this.$message.error('密码错误')
        }
      }
    },
    chosePaper (val) {
      this.multiSelection = val
    },
    searchInfo () {
      this.$emit('changeStepNextBack', 2)
    }
  }
}
</script>

<style lang="scss" scoped>
.payTransfer {
  /deep/ .el-input__inner {
    padding: 0 5px;
  }
  .w-400 {
    width: 400px;
  }
  .header {
    padding: 20px 10px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
  }
  .pay-transfer-header {
    background-color: #f8f8f8;
    padding: 5px 10px;
  }

  .password-input {
    position: absolute;
    left: 119px;
    width: 80px;
    top: 5px;
  }
  .password-button {
    position: absolute;
    left: 260px;
    width: 75px;
    height: 25px;
    top: 155px;
    cursor: pointer;
  }
  .content .makingContent .rightBottomLine {
    border-bottom: 1px #c5d7e8 solid;
    border-right: 1px #c5d7e8 solid;
  }
  .content .makingContent td {
    height: 40px;
    font-size: 15px;
    text-align: center;
  }
  .content .makingContent .BottomLine {
    border-bottom: 1px #c5d7e8 solid;
  }
}
</style>
