<template>
  <div class="payTransfer">
    <el-table :data="detailData" border highlight-current-row fit size="mini" @selection-change="chosePaper" v-if="stepNextBack === 1">
      <el-table-column type="selection" align="center" />
      <el-table-column label="类别优先级" prop="levalSet" align="center"></el-table-column>
      <el-table-column label="账户类别名称" prop="accountType" align="center"></el-table-column>
    </el-table>

    <el-form ref="form" :model="pwdForm" :rules="rule" label-width="200px" v-if="stepNextBack === 1" style="margin-top: 15px;">
      <div class="pay-transfer-header mb-20">请输入认证信息</div>

      <el-form-item label="交易密码：" prop="password">
        <el-input type="password" size="mini" style="width:260px;" v-model="pwdForm.password"></el-input>
      </el-form-item>
      <el-form-item style="margin-top: 20px;">
        <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="addInfo">新增</el-button>
        <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="delInfo">删除</el-button>
      </el-form-item>
    </el-form>

    <el-form ref="addForm" :model="addForm" :rules="rule" label-width="200px" v-if="stepNextBack === 2" style="margin-top: 15px;">
      <el-form-item label="客户号：">
        <span>{{ companyInfo.number }}</span>
      </el-form-item>
      <el-form-item label="类别优先级：">
        <el-input type="text" size="mini" style="width:260px;" v-model="addForm.levalSet"></el-input>
        <span style="margin-left: 15px;">优先级可以是0-9999的整数，优先级由0至9999依次降低</span>
      </el-form-item>
      <el-form-item label="账户类别名称：">
        <el-input type="text" size="mini" style="width:260px;" v-model="addForm.accountType"></el-input>
        <span style="margin-left: 15px;">账户类别名称可以根据需要自行设置类别名称</span>
      </el-form-item>
      <div class="pay-transfer-header mb-20">请输入认证信息</div>

      <el-form-item label="交易密码：" prop="password">
        <el-input type="password" size="mini" style="width:260px;" v-model="addForm.password" auto-complete="new-password"></el-input>
      </el-form-item>
      <el-form-item style="margin-top: 20px;">
        <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="saveInfo">确定</el-button>
        <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="backStep">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import elDragDialog from '@/directive/el-drag-dialog' // base on element-ui
export default {
  name: 'type',
  directives: { elDragDialog },
  computed: {
    companyInfo () {
      return this.$store.state.companyInfo
    },
    typeInfo () {
      return this.$store.state.typeInfo
    },
    accountData () {
      return this.$store.state.payeeInfo
    }
  },
  props: {
    stepNextBack: {
      type: Number,
      required: true,
      defaults: 1
    }
  },
  watch: {
    typeInfo (value) {
      this.detailData = value
    }
  },
  data () {
    return {
      multiSelection: [],
      detailData: [],
      pwdForm: {
        password: ''
      },
      addForm: {
        levalSet: '',
        accountType: '',
        password: ''
      },
      rule: {
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.getList()
  },
  methods: {
    addInfo () {
      this.$emit('changeStepNextBack', 2)
    },
    backStep () {
      this.getList()
      this.$emit('changeStepNextBack', 1)
    },
    delInfo () {
      if (this.multiSelection.length === 0) {
        this.$message.warning('请选择需要删除数据')
      } else {
        if (this.pwdForm.password === this.$store.state.userInfo.password) {
          // 判断是否被用，用了的不允许删除
          let _f = false
          for (const one of this.accountData) {
            for (const two of this.multiSelection) {
              if (one.accountType === two.accountType) {
                _f = true
              }
            }
          }

          if (_f) {
            this.$message.warning('要删除类型已被使用，请重新选择！')
            return false
          }
          this.$confirm('此操作将删除类别数据, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            const data = []
            this.multiSelection.forEach(v => {
              data.push(v.levalSet)
            })
            this.$store.commit('delTypeInfo', data)
            this.$message.success('删除成功')
            this.pwdForm.password = ''
          }).catch(() => {
            console.log('cancel')
          })
        } else {
          this.$message.warning('密码错误')
        }
      }
    },
    saveInfo () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          if (this.addForm.password === this.$store.state.userInfo.password) {
            delete this.addForm.password

            const data = { ...this.addForm }
            // 检测是否重复
            let bool = true
            if (this.typeInfo.length > 0) {
              this.typeInfo.forEach(v => {
                if (v.accountType === data.accountType) {
                  bool = false
                }
              })
            }
            if (!bool) {
              this.$message.warning('账户类别名称不允许重复')
              return false
            }
            // if (data.id >= 0) {
            //   // 编辑
            //   this.$store.commit('editPayeeInfo', data)
            //   this.$message.success('编辑成功')
            //   this.resetPayee()
            // } else {
            // 新增
            this.$store.commit('saveTypeInfo', data)
            this.$message.success('新增成功')
            // }
            this.dialogConfirmVisible = false
            this.dialogUKVisible = false
            delete this.addForm.id
            this.resetAddForm()
            this.$refs.addForm.resetFields()
          } else {
            this.$message.error('密码错误')
          }
        }
      })
    },
    getList () {
      this.detailData = this.typeInfo

      this.detailData.sort(this.compare('levalSet'))
    },
    chosePaper (val) {
      this.multiSelection = val
    },
    compare (attr) {
      return function (a, b) {
        var val1 = a[attr]
        var val2 = b[attr]
        return val1 - val2
        // return val2 - val1
      }
    },
    resetAddForm () {
      this.addForm = {
        levalSet: '',
        accountType: '',
        password: ''
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.payTransfer {
  /deep/ .el-input__inner {
    padding: 0 5px;
  }
  .w-400 {
    width: 400px;
  }
  .header {
    padding: 20px 10px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
  }
  .pay-transfer-header {
    background-color: #f8f8f8;
    padding: 5px 10px;
  }

  .password-input {
    position: absolute;
    left: 119px;
    width: 80px;
    top: 5px;
  }
  .password-button {
    position: absolute;
    left: 260px;
    width: 75px;
    height: 25px;
    top: 155px;
    cursor: pointer;
  }
  .content .makingContent .rightBottomLine{
    border-bottom: 1px #c5d7e8 solid;
    border-right: 1px #c5d7e8 solid;
  }
  .content .makingContent td {
    height: 40px;
    font-size: 15px;
    text-align: center;
  }
  .content .makingContent .BottomLine {
    border-bottom: 1px #c5d7e8 solid;
  }
}
</style>
