    <div class="header">
      <div style="float:left;margin-top: -5px;">
        <span style="font-size:16px;">当前位置:</span>
      </div>
      <div style="padding-left: 80px;">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>转账业务</el-breadcrumb-item>
          <el-breadcrumb-item>常用账户管理</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    </div>
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="类别维护" name="type">
        <type :stepNextBack="nextStep" @changeStepNextBack="changeStepNextBack"></type>
      </el-tab-pane>
      <el-tab-pane label="查询" name="search">
        <search :stepNextBack="nextStep" @changeStepNextBack="changeStepNextBack"></search>
      </el-tab-pane>
      <el-tab-pane label="新增" name="add">
        <add></add>
      </el-tab-pane>
      <el-tab-pane label="修改" name="modify">
        <modify :stepNextBack="nextStep" @changeStepNextBack="changeStepNextBack"></modify>
      </el-tab-pane>
      <el-tab-pane label="删除" name="delInfo">
        <delInfo :stepNextBack="nextStep" @changeStepNextBack="changeStepNextBack"></delInfo>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import elDragDialog from '@/directive/el-drag-dialog' // base on element-ui
import type from '@/views/pay/components/type'
import search from '@/views/pay/components/search'
import add from '@/views/pay/components/add'
import modify from '@/views/pay/components/modify'
import delInfo from '@/views/pay/components/del'
export default {
  name: 'PayTransfer',
  components: {
    type,
    search,
    add,
    modify,
    delInfo
  },
  directives: { elDragDialog },
  data () {
    return {
      activeName: 'search',
      nextStep: 1
    }
  },
  methods: {
    handleClick () {
      this.nextStep = 1
    },
    changeStepNextBack (val) {
      this.nextStep = val
    }
  }
}
</script>

